.Switcher {
  position: fixed;
  bottom: 1rem;
  left: 50%;
  transform: translate3d(-50%, 0px, 0px);
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.5);
  width: 80px;
  height: 26px;
  background: #333;
  margin: 20px auto;
  border-radius: 50px;
  box-shadow: inset 0px 1px 1px rgba(0, 0, 0, 0.5), 0px 1px 0px rgba(255, 255, 255, 0.2);
}

